"use client";

import React, { FC, MutableRefObject, useEffect, useRef, useState } from 'react';
import { mat4, vec3 } from 'gl-matrix';
import ArcballControl from './infinite-menu/control';
import { DiscGeometry, IcosahedronGeometry } from './infinite-menu/geometry';
import { createAndSetupTexture, createProgram, makeBuffer, makeVertexArray, resizeCanvasToDisplaySize } from './infinite-menu/webgl';
import { discFragShaderSource, discVertShaderSource } from './infinite-menu/shaders';

interface MenuItem {
  image: string;
  link: string;
  title: string;
  description: string;
}

interface InfiniteMenuProps {
  items?: MenuItem[];
}

interface Camera {
  matrix: mat4;
  near: number;
  far: number;
  fov: number;
  aspect: number;
  position: vec3;
  up: vec3;
  matrices: {
    view: mat4;
    projection: mat4;
    inversProjection: mat4;
  };
}

const defaultItems: MenuItem[] = [
  {
    image: 'https://picsum.photos/900/900?grayscale',
    link: 'https://google.com/',
    title: '',
    description: ''
  }
];

const TARGET_FRAME_DURATION = 1000 / 60;

const InfiniteMenu: FC<InfiniteMenuProps> = ({ items = [] }) => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null) as MutableRefObject<HTMLCanvasElement | null>;
  const [activeItem, setActiveItem] = useState<MenuItem | null>(null);
  const [isMoving, setIsMoving] = useState<boolean>(false);

  useEffect(() => {
    if (items && items.length && !activeItem) {
      setActiveItem(items[0]);
    }
  }, [items]);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const gl = canvas.getContext('webgl2', { antialias: true, alpha: false });
    if (!gl) return;

    const control = new ArcballControl(canvas);

    // Camera
    const camera: Camera = {
      matrix: mat4.create(),
      near: 0.1,
      far: 40,
      fov: Math.PI / 4,
      aspect: 1,
      position: vec3.fromValues(0, 0, 3),
      up: vec3.fromValues(0, 1, 0),
      matrices: {
        view: mat4.create(),
        projection: mat4.create(),
        inversProjection: mat4.create()
      }
    };

    function updateCameraMatrix() {
      mat4.targetTo(camera.matrix, camera.position, [0, 0, 0], camera.up);
      mat4.invert(camera.matrices.view, camera.matrix);
    }

    function updateProjectionMatrix() {
      camera.aspect = canvas.clientWidth / canvas.clientHeight;
      const height = 2 * 0.35;
      const distance = camera.position[2];
      if (camera.aspect > 1) {
        camera.fov = 2 * Math.atan(height / distance);
      } else {
        camera.fov = 2 * Math.atan(height / camera.aspect / distance);
      }
      mat4.perspective(camera.matrices.projection, camera.fov, camera.aspect, camera.near, camera.far);
      mat4.invert(camera.matrices.inversProjection, camera.matrices.projection);
    }

    // Program
    const program = createProgram(gl, [discVertShaderSource, discFragShaderSource], null, {
      aModelPosition: 0,
      aModelNormal: 1,
      aModelUvs: 2,
      aInstanceMatrix: 3
    });
    if (!program) return;

    const loc = {
      aModelPosition: gl.getAttribLocation(program, 'aModelPosition'),
      aModelUvs: gl.getAttribLocation(program, 'aModelUvs'),
      aInstanceMatrix: gl.getAttribLocation(program, 'aInstanceMatrix'),
      uWorldMatrix: gl.getUniformLocation(program, 'uWorldMatrix'),
      uViewMatrix: gl.getUniformLocation(program, 'uViewMatrix'),
      uProjectionMatrix: gl.getUniformLocation(program, 'uProjectionMatrix'),
      uCameraPosition: gl.getUniformLocation(program, 'uCameraPosition'),
      uRotationAxisVelocity: gl.getUniformLocation(program, 'uRotationAxisVelocity'),
      uTex: gl.getUniformLocation(program, 'uTex'),
      uItemCount: gl.getUniformLocation(program, 'uItemCount'),
      uAtlasSize: gl.getUniformLocation(program, 'uAtlasSize')
    } as const;

    // Geometry buffers
    const discGeo = new DiscGeometry(56, 1);
    const discData = discGeo.data;
    const vao = makeVertexArray(
      gl,
      [
        [makeBuffer(gl, discData.vertices, gl.STATIC_DRAW), loc.aModelPosition, 3],
        [makeBuffer(gl, discData.uvs, gl.STATIC_DRAW), loc.aModelUvs, 2]
      ],
      discData.indices
    );
    if (!vao) return;

    // Instances from subdivided icosahedron
    const ico = new IcosahedronGeometry();
    ico.subdivide(1).spherize(2);
    const instancePositions = ico.vertices.map((v) => v.position);
    const INSTANCE_COUNT = instancePositions.length;

    const matricesArray = new Float32Array(INSTANCE_COUNT * 16);
    const matrices: Float32Array[] = [];
    for (let i = 0; i < INSTANCE_COUNT; ++i) {
      const m = new Float32Array(matricesArray.buffer, i * 16 * 4, 16);
      mat4.identity(m as unknown as mat4);
      matrices.push(m);
    }

    const instanceBuffer = gl.createBuffer();
    gl.bindVertexArray(vao);
    gl.bindBuffer(gl.ARRAY_BUFFER, instanceBuffer);
    gl.bufferData(gl.ARRAY_BUFFER, matricesArray.byteLength, gl.DYNAMIC_DRAW);
    const bytesPerMatrix = 16 * 4;
    for (let j = 0; j < 4; ++j) {
      const aLoc = loc.aInstanceMatrix + j;
      gl.enableVertexAttribArray(aLoc);
      gl.vertexAttribPointer(aLoc, 4, gl.FLOAT, false, bytesPerMatrix, j * 4 * 4);
      gl.vertexAttribDivisor(aLoc, 1);
    }
    gl.bindBuffer(gl.ARRAY_BUFFER, null);
    gl.bindVertexArray(null);

    // Texture atlas
    const allItems = items.length ? items : defaultItems;
    const itemCount = Math.max(1, allItems.length);
    let atlasSize = Math.ceil(Math.sqrt(itemCount));
    const cellSize = 512;
    const atlasCanvas = document.createElement('canvas');
    const ctx = atlasCanvas.getContext('2d')!;
    atlasCanvas.width = atlasSize * cellSize;
    atlasCanvas.height = atlasSize * cellSize;

    const tex = createAndSetupTexture(gl, gl.LINEAR, gl.LINEAR, gl.CLAMP_TO_EDGE, gl.CLAMP_TO_EDGE);

    Promise.all(
      allItems.map(
        (item) =>
          new Promise<HTMLImageElement>((resolve) => {
            const img = new Image();
            img.crossOrigin = 'anonymous';
            img.onload = () => resolve(img);
            img.src = item.image;
          })
      )
    ).then((images) => {
      images.forEach((img, i) => {
        const x = (i % atlasSize) * cellSize;
        const y = Math.floor(i / atlasSize) * cellSize;
        ctx.drawImage(img, x, y, cellSize, cellSize);
      });
      gl.bindTexture(gl.TEXTURE_2D, tex);
      gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA, gl.RGBA, gl.UNSIGNED_BYTE, atlasCanvas);
      gl.generateMipmap(gl.TEXTURE_2D);
    });

    const world = mat4.create();

    let timePrev = 0;
    let smoothRotationVelocity = 0;

    function animate(dt: number) {
      control.update(dt, TARGET_FRAME_DURATION);
      const positions = instancePositions.map((p) => vec3.transformQuat(vec3.create(), p, control.orientation));
      const scale = 0.25;
      const SCALE_INTENSITY = 0.6;

      positions.forEach((p, ndx) => {
        const s = (Math.abs(p[2]) / 2) * SCALE_INTENSITY + (1 - SCALE_INTENSITY);
        const finalScale = s * scale;
        const matrix = mat4.create();
        mat4.multiply(matrix, matrix, mat4.fromTranslation(mat4.create(), vec3.negate(vec3.create(), p)));
        mat4.multiply(matrix, matrix, mat4.targetTo(mat4.create(), [0, 0, 0], p, [0, 1, 0]));
        mat4.multiply(matrix, matrix, mat4.fromScaling(mat4.create(), [finalScale, finalScale, finalScale]));
        mat4.multiply(matrix, matrix, mat4.fromTranslation(mat4.create(), [0, 0, -2]));
        mat4.copy(matrices[ndx], matrix);
      });

      gl.bindBuffer(gl.ARRAY_BUFFER, instanceBuffer);
      gl.bufferSubData(gl.ARRAY_BUFFER, 0, matricesArray);
      gl.bindBuffer(gl.ARRAY_BUFFER, null);

      smoothRotationVelocity = smoothRotationVelocity + (control.rotationVelocity - smoothRotationVelocity) * 0.5;
    }

    function render() {
      gl.useProgram(program);
      gl.enable(gl.CULL_FACE);
      gl.enable(gl.DEPTH_TEST);
      gl.clearColor(0, 0, 0, 0);
      gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);

      gl.uniformMatrix4fv(loc.uWorldMatrix, false, world);
      gl.uniformMatrix4fv(loc.uViewMatrix, false, camera.matrices.view);
      gl.uniformMatrix4fv(loc.uProjectionMatrix, false, camera.matrices.projection);
      gl.uniform3f(loc.uCameraPosition, camera.position[0], camera.position[1], camera.position[2]);
      gl.uniform4f(
        loc.uRotationAxisVelocity,
        control.rotationAxis[0],
        control.rotationAxis[1],
        control.rotationAxis[2],
        smoothRotationVelocity * 1.1
      );
      gl.uniform1i(loc.uItemCount, allItems.length);
      gl.uniform1i(loc.uAtlasSize, atlasSize);
      gl.uniform1i(loc.uTex, 0);
      gl.activeTexture(gl.TEXTURE0);
      gl.bindTexture(gl.TEXTURE_2D, tex);

      gl.bindVertexArray(vao);
      gl.drawElementsInstanced(gl.TRIANGLES, discData.indices.length, gl.UNSIGNED_SHORT, 0, INSTANCE_COUNT);
      gl.bindVertexArray(null);
    }

    function frame(time: number) {
      const dt = Math.min(32, time - timePrev);
      timePrev = time;
      animate(dt);
      render();
      requestAnimationFrame(frame);
    }

    function resize() {
      const needs = resizeCanvasToDisplaySize(canvas);
      if (needs) gl.viewport(0, 0, gl.drawingBufferWidth, gl.drawingBufferHeight);
      updateProjectionMatrix();
    }

    updateCameraMatrix();
    updateProjectionMatrix();
    resize();

    const onResize = () => resize();
    window.addEventListener('resize', onResize);
    requestAnimationFrame(frame);

    return () => {
      window.removeEventListener('resize', onResize);
    };
  }, [items]);

  const handleButtonClick = () => {
    if (!activeItem?.link) return;
    if (activeItem.link.startsWith('http')) {
      window.open(activeItem.link, '_blank');
    } else {
      console.log('Internal route:', activeItem.link);
    }
  };

  return (
    <div className="relative w-full h-full">
      <canvas
        id="infinite-grid-menu-canvas"
        ref={canvasRef}
        className="cursor-grab w-full h-full overflow-hidden relative outline-none active:cursor-grabbing"
      />
      {activeItem && (
        <>
          <h2 className="select-none absolute font-black left-[1.6em] top-1/2 transform translate-x-[20%] -translate-y-1/2 text-4xl">
            {activeItem.title}
          </h2>
          <p className="select-none absolute max-w-[10ch] text-[1.2rem] top-1/2 right-[1%] translate-x-[-90%] -translate-y-1/2">
            {activeItem.description}
          </p>
          <div
            onClick={handleButtonClick}
            className="absolute left-1/2 z-10 w-[60px] h-[60px] grid place-items-center bg-[#00ffff] border-[5px] border-black rounded-full cursor-pointer -translate-x-1/2 bottom-[3.8em]"
          >
            <p className="select-none relative text-[#060010] top-[2px] text-[26px]"></p>
          </div>
        </>
      )}
    </div>
  );
};

export default InfiniteMenu;
